<template>
    <div class="topic-dialog">
        <el-dialog
                title="发布吐槽"
                :visible.sync="show">
            <div>
                <vue-ueditor-wrap v-model="msg" :config="myConfig"></vue-ueditor-wrap>
                <el-button type="primary" @click="topicPublish">发布</el-button>
            </div>

        </el-dialog>
    </div>
</template>

<script>
    import VueUeditorWrap from 'vue-ueditor-wrap'
    import {createTopic, getTopic} from '@/service/info-api'
    import {megSuc} from "../../utils/message";

    export default {
        name: "publishTopic",
        props: ['dialogTableVisible', 'detailList'],
        components: {
            VueUeditorWrap
        },
        data(){
            return {
                show: true,
                msg: '',
                myConfig: {
                    // 编辑器不自动被内容撑高
                    autoHeightEnabled: false,
                    // 初始容器高度
                    initialFrameHeight: 240,
                    // 初始容器宽度
                    initialFrameWidth: '100%',
                    // 上传文件接口（这个地址是我为了方便各位体验文件上传功能搭建的临时接口，请勿在生产环境使用！！！）
                    serverUrl: 'api/upload/img',
                    // UEditor 资源文件的存放路径，如果你使用的是 vue-cli 生成的项目，通常不需要设置该选项，vue-ueditor-wrap 会自动处理常见的情况，如果需要特殊配置，参考下方的常见问题2
                    UEDITOR_HOME_URL: '/UEditor/'
                }
            }
        },
        methods: {
            topicPublish(){
                createTopic({text: this.msg})
                .then((res) => {
                    if (res.code === 2000){
                        megSuc('发布成功')
                        getTopic()
                        .then((res) => {
                            if (res.code === 2000){
                                this.$emit('update:detailList', res.topic_list)
                                this.$emit('update:dialogTableVisible', false)
                            }
                        })


                    }
                })

            }
        }
    }
</script>

<style lang="scss">
    .topic-dialog{
        position: absolute;
        .el-button{
            margin-top: 5px;
        }
    }

</style>
